import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import {TERC20, LTokenOption, PTokenOption, EverlastingOption} from './utils/ABI.json'
import PoolInfo from './components/PoolInfo'
import Symbols from './components/Symbols'
import AccountInfo from './components/AccountInfo'
import Positions from './components/Positions'
import Actions from './components/Actions'

function fromWei(value, unit='ether') {
    return Web3.utils.fromWei(value, unit)
}

function toWei(value, unit='ether') {
    return Web3.utils.toWei(value, unit)
}

function fromWeiUSDT(value, unit='Mwei') {
    return Web3.utils.fromWei(value, unit)
}

const getSignature = async ({symbol}) => {
  try {
      let url = `https://oracle2.deri.finance/get_signed_volatility?symbol=VOL-${symbol}`
      let res = await fetch(url);
      let oracle = await res.json();
      return {
          timestamp: oracle.data.timestamp,
          r: oracle.data.r,
          s: oracle.data.s,
          v: oracle.data.v,
          price: oracle.data.volatility}
  } catch (err) {
      console.log(err);
  }
}

const signedPrice = async () => {
        let oracle_btc = await getSignature({symbol:"BTCUSD"})
        let oracle_eth = await getSignature({symbol: "ETHUSD"})
        let signed = [
            {symbolId: 0, timestamp: oracle_btc.timestamp,
                price: oracle_btc.price,
                v: oracle_btc.v,
                r: oracle_btc.r,
                s: oracle_btc.s},
            // {symbolId: 1, timestamp: oracle_btc.timestamp,
            //     price: oracle_btc.price,
            //     v: oracle_btc.v,
            //     r: oracle_btc.r,
            //     s: oracle_btc.s},
            {symbolId: 6, timestamp: oracle_eth.timestamp,
                price: oracle_eth.price,
                v: oracle_eth.v,
                r: oracle_eth.r,
                s: oracle_eth.s},
            ]
        return signed
    }


// deployer 0x4aF582Fd437f997F08df645aB6b0c070CC791DeE
// usdt.address 0x2ebE70929bC7D930248040f54135dA12f458690C
// lToken.address 0xCeBF39aF5e8D9736985ddCb6E45c016Fd146218C
// pToken.address 0x7484e22022C971e314A00e0dfbfCDe8E223c80aC
// pricing.address 0x1D7AFF20BB5E52dDD10d5B4B0e0b91b5327Ae826
// pool.address 0x98EfC36182eEC80eC20F600533E87f82AeDbb2e6
// volatilityOracleBTC.address 0x7A4701A1A93BB7692351aEBcD4F5Fab1d4377BBc
// volatilityOracleETH.address 0xF03fDB7193826E11310DE6e297826c4E29E898B9
// oracleBTCUSD 0x18C036Ee25E205c224bD78f10aaf78715a2B6Ff1


function App() {
    const web3 = new Web3(window.ethereum)
    const bToken = new web3.eth.Contract(TERC20,            '0x2ebE70929bC7D930248040f54135dA12f458690C')
    const lToken = new web3.eth.Contract(LTokenOption,       '0xCeBF39aF5e8D9736985ddCb6E45c016Fd146218C')
    const pToken = new web3.eth.Contract(PTokenOption,       '0x7484e22022C971e314A00e0dfbfCDe8E223c80aC')
    const pool = new web3.eth.Contract(EverlastingOption,  '0x98EfC36182eEC80eC20F600533E87f82AeDbb2e6')

    const [data, setData] = useState({
        account: '',
        poolinfo: {},
        symbols: [[]],
        accountInfo: {},
        positions: []
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = await getAccount()
        const poolinfo = await getPoolInfo()
        const symbols = await getSymbols(poolinfo.activeSymbolIds)
        const accountInfo = await getAccountInfo(account)
        const positions = await getPositions(account, poolinfo.activeSymbolIds)
        setData({account, poolinfo, symbols, accountInfo, positions})
    }

    const getAccount = async () => {
        return Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
    }

    const getPoolInfo = async () => {
        const balance = fromWeiUSDT(await bToken.methods.balanceOf(pool.options.address).call())
        const lTokenTotalSupply = fromWei(await lToken.methods.totalSupply().call())
        const pTokenTotalSupply = await pToken.methods.totalSupply().call()
        const activeSymbolIds = await pToken.methods.getActiveSymbolIds().call()
        const numPositionHolders = await Promise.all(activeSymbolIds.map(async (id) => {return await pToken.methods.getNumPositionHolders(id).call()}))
        const liquidity = fromWei(await pool.methods.getLiquidity().call())
        const protocolFeeAccrued = fromWei(await pool.methods.getProtocolFeeAccrued().call())
        return {
            balance,
            lTokenTotalSupply,
            pTokenTotalSupply,
            activeSymbolIds,
            numPositionHolders,
            liquidity,
            protocolFeeAccrued
        }
    }


    const getSymbols = async (activeSymbolIds) => {
        return await Promise.all(activeSymbolIds.map(async (id) => {
            let s = await pool.methods.getSymbol(id).call()
            return [
                s[0],
                s[1],
                s[2],
                s[3],
                fromWei(s[4]),
                fromWei(s[5]),
                fromWei(s[6]),
                s[7]==true? "True" : "False",
                fromWei(s[8]),
                fromWei(s[9]),
                fromWei(s[10]),
                fromWei(s[11]),
                fromWei(s[12]),
                fromWei(s[13]),
                fromWei(s[14]),
                fromWei(s[15]),
                fromWei(s[16])
            ]
        }))
    }

    const getAccountInfo = async (account) => {
        const balance = fromWei(await bToken.methods.balanceOf(account).call())
        const lTokenBalance = fromWei(await lToken.methods.balanceOf(account).call())
        const margin = fromWei(await pToken.methods.getMargin(account).call())
        return {
            balance,
            lTokenBalance,
            margin
        }
    }

    const getPositions = async (account, activeSymbolIds) => {
        return await Promise.all(activeSymbolIds.map(async (id) => {
            let p = await pToken.methods.getPosition(account, id).call()
            return [
                fromWei(p[0]),
                fromWei(p[1]),
                fromWei(p[2]),
                fromWei(p[3])
            ]
        }))
    }

    const onMint = async () => {
        await bToken.methods.mint(data.account, "100000000000000000000000").send({from: data.account})
        await update()
    }

    const onApprove = async () => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        await bToken.methods.approve(pool.options.address, max).send({from: data.account})
    }



    const onAddLiquidity = async (value) => {
        await pool.methods.addLiquidity(toWei(value), []).send({from: data.account})
        await update()
    }

    const onAddLiquidityUpdate = async (value) => {
        let signed = await signedPrice()
        console.log("signed price", signed)
        await pool.methods.addLiquidity(toWei(value), signed).send({from: data.account})
    }


    const onRemoveLiquidity = async (value) => {
        let signed = await signedPrice()
        await pool.methods.removeLiquidity(toWei(value), signed).send({from: data.account})
        await update()
    }

    const onAddMargin = async (value) => {
        await pool.methods.addMargin(toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveMargin = async (value) => {
        let signed = await signedPrice()
        await pool.methods.removeMargin(toWei(value), signed).send({from: data.account})
        await update()
    }

    const onTrade = async (symbolId, volume) => {
        let signed = await signedPrice()
        await pool.methods.trade(symbolId, toWei(volume), signed).send({from: data.account})
        await update()
    }

    const onLiquidate = async (address) => {
        let signed = await signedPrice()
        await pool.methods.liquidate(address, signed).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV2 EverlastingOption BSC Testnet</h1>
            <PoolInfo poolinfo={data.poolinfo} />
            <Symbols symbols={data.symbols} />
            <AccountInfo accountInfo={data.accountInfo} />
            <Positions symbols={data.symbols} positions={data.positions} />
            <Actions
                onMint={onMint}
                onApprove={onApprove}
                onAddLiquidity={onAddLiquidityUpdate}
                onRemoveLiquidity={onRemoveLiquidity}
                onAddMargin={onAddMargin}
                onRemoveMargin={onRemoveMargin}
                onTrade={onTrade}
                onLiquidate={onLiquidate}
            />
        </div>
    );
}

export default App;
